.card {
  height: 100px;
  width: 300px;
  margin: 10px;
  border: 1px solid grey;
}

.symbol {
  text-align: right;
}
.fas {
  cursor: pointer;
}
.bt {
  color: red;
  margin: 10px;
}

.name {
  padding: 0px 60px;
  text-transform: uppercase;
  font-size: 20px;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
}
